@import url("https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap");
@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0; }
  8.3%, 25% {
    left: 20%;
    opacity: 1; }
  33.33%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0; }
  41.63%, 58.29% {
    left: 20%;
    opacity: 1; }
  66.66%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0; }
  74.96%, 91.62% {
    left: 20%;
    opacity: 1; }
  100% {
    left: 110%;
    opacity: 0; } }

#menu {
  position: fixed;
  top: 0;
  right: 0;
  margin: 0em 2em;
  z-index: 1;
  cursor: pointer;
  padding: .5em; }
  #menu > div {
    width: 35px;
    height: 3px;
    background-color: #FFF;
    margin: 6px 0;
    box-shadow: 2px 2px #000; }
