@import url("https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap");
@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0; }
  8.3%, 25% {
    left: 20%;
    opacity: 1; }
  33.33%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0; }
  41.63%, 58.29% {
    left: 20%;
    opacity: 1; }
  66.66%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0; }
  74.96%, 91.62% {
    left: 20%;
    opacity: 1; }
  100% {
    left: 110%;
    opacity: 0; } }

.Bar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 5px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #BB0A30;
  z-index: 2;
  box-shadow: 3px 3px 10px; }
  .Bar > h3 {
    color: #FFF;
    margin: 0px 5px;
    font-size: 2em;
    text-shadow: 2px 5px #000; }
  .Bar > #menu {
    position: relative;
    margin: 0px 5px; }
