@import url("https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap");
@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0; }
  8.3%, 25% {
    left: 20%;
    opacity: 1; }
  33.33%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0; }
  41.63%, 58.29% {
    left: 20%;
    opacity: 1; }
  66.66%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0; }
  74.96%, 91.62% {
    left: 20%;
    opacity: 1; }
  100% {
    left: 110%;
    opacity: 0; } }

#nav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 2;
  right: -10px;
  top: 0;
  background-color: rgba(0, 0, 0, 0.9);
  overflow-x: hidden;
  transition: .5s;
  box-shadow: -3px 0px 5px 5px rgba(0, 0, 0, 0.9);
  z-index: 3; }
  #nav .closeBtn {
    position: absolute;
    display: block;
    text-decoration: none;
    color: #FFF;
    top: 20px;
    left: 45px;
    font-size: 60px; }
  #nav .overlay-content {
    position: relative;
    top: 25%;
    width: 100%;
    text-align: center;
    margin-top: 30px; }
    #nav .overlay-content > a {
      padding: 8px;
      text-decoration: none;
      font-size: 36px;
      color: #FFF;
      display: block;
      transition: .3s; }
      #nav .overlay-content > a:hover {
        background: #F1F1F1;
        color: #000; }
