@import url("https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap");
@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0; }
  8.3%, 25% {
    left: 20%;
    opacity: 1; }
  33.33%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0; }
  41.63%, 58.29% {
    left: 20%;
    opacity: 1; }
  66.66%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0; }
  74.96%, 91.62% {
    left: 20%;
    opacity: 1; }
  100% {
    left: 110%;
    opacity: 0; } }

h1 {
  position: relative;
  color: #FFF;
  font-size: 4em;
  transform: rotate(-13deg);
  opacity: .8; }
  h1:after {
    content: "";
    display: block;
    position: absolute;
    top: .8em;
    left: 0;
    width: 100%;
    height: 100%;
    border-bottom: .1em solid;
    border-radius: 60%; }
  h1:before {
    content: " ";
    display: block;
    position: absolute;
    top: -.6em;
    left: 0;
    width: 100%;
    height: 100%;
    border-top: .1em solid;
    border-radius: 60%; }

.box {
  position: absolute;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  transition: 3s;
  overflow: hidden; }
  .box #logo {
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    top: 5em;
    left: 12em; }

#curtain {
  background-image: url("/static/images/26227.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  box-sizing: border-box; }
  #curtain .darkness {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    background: rgba(0, 0, 0, 0.4); }
  #curtain #info {
    overflow: hidden;
    display: flex;
    position: absolute;
    bottom: 150px;
    left: 0px;
    height: 10em;
    width: 100%;
    justify-content: center;
    align-items: center; }
    #curtain #info .item-1,
    #curtain #info .item-2,
    #curtain #info .item-3 {
      position: absolute;
      display: block;
      width: 60%;
      font-size: 2.35em;
      animation-duration: 20s;
      text-align: center;
      animation-timing-function: ease-in-out;
      animation-iteration-count: infinite;
      color: #CECECE; }
    #curtain #info .item-1 {
      animation-name: anim-1; }
    #curtain #info .item-2 {
      animation-name: anim-2; }
    #curtain #info .item-3 {
      animation-name: anim-3; }
