@import url("https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap");
@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0; }
  8.3%, 25% {
    left: 20%;
    opacity: 1; }
  33.33%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0; }
  41.63%, 58.29% {
    left: 20%;
    opacity: 1; }
  66.66%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0; }
  74.96%, 91.62% {
    left: 20%;
    opacity: 1; }
  100% {
    left: 110%;
    opacity: 0; } }

* {
  box-sizing: border-box; }

html, body, #__next {
  height: 100%;
  width: 100%; }

body {
  padding: 0;
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  margin: 0px;
  background: #e1e2e1; }

#root {
  display: flex;
  flex-direction: column; }

/*-----------SCROLLBAR-------------*/
::-webkit-scrollbar {
  width: 16px;
  background-color: #000; }

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  background-color: #000; }

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #a00f2d;
  border-radius: 10px; }

::-webkit-scrollbar-thumb:hover {
  background: #e61541; }
